import React from "react";
import {  Route, Switch } from 'react-router-dom';
import Footer from "./Components/footer.js";
import Header from "./Components/header.js";
import Home from "./Components/home.js";
import Navbar from "./Components/navbar.js";
import Pageslink_navbar from "./Components/pageslink_navbar.js";
import ValueFilter from "./Components/valuefilter.js";
import PostDetail from "./Components/postDetailbody.js";
import Product from "./Components/product.js"
import companyfilter from "./Components/companyfilter.js";
function App() {
   
  return (
      <main>
         <Navbar/>
         <Header/> 
         <Pageslink_navbar/>
          <Switch>
              <Route path="/home" component={Home} exact />
              <Route path="/product" component={Product} exact/>
              <Route path="/valuefilter" component={ValueFilter} exact/>
              <Route path="/companyfilter" component={companyfilter} exact/>
              <Route path="/postDetail" component={PostDetail} exact/>
          </Switch>
          <Footer/>
      </main>
  )
}
export default App;
