import React ,{useState}from "react";
import { Link,NavLink, withRouter  } from "react-router-dom";
const Pageslink_navbar =() =>{
const [show,setShow] = useState(false);

  return (<>
  <section className="navbar_bg1">
  <nav class="navbar navbar-expand-lg navbar-light ">
  <div class="container">
    
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"
    onClick={()=>setShow(!show)}>
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class={`collapse navbar-collapse ${show?"show":""}`} >
      <ul class="navbar-nav ml-auto mb-2 mb-lg-0">
        <li class="nav-item">
          <Link  className="nav-link" aria-current="page"  exact to="/Home">홈</Link>
        </li>
        
        <div class="dropdown">
  <NavLink class="btn btn-light dropdown-toggle span " type="button" activeClassName="main-nav-active"  to="/product" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
  제품별★
  </NavLink>
  <div class="dropdown-menu dropdown-multicol2" aria-labelledby="dropdownMenuButton">
    <div class="dropdown-col">
      <a class="dropdown-item span" href="#">음식/식품</a>
      <a class="dropdown-item span" href="#">의류</a>
      <a class="dropdown-item span" href="#">화장품</a>
      <a class="dropdown-item span" href="#">공예</a>
      <a class="dropdown-item span" href="#">가구</a>
    </div>
    <div class="dropdown-col">
      <a class="dropdown-item span" href="#">디지털/가전</a>
      <a class="dropdown-item span" href="#">의료품</a>
      <a class="dropdown-item span" href="#">생활 용품</a>
      <a class="dropdown-item span" href="#">애견 용품</a>
      <a class="dropdown-item span" href="#">사무 용품</a>
    </div>
    
  </div>
</div>
        
        <li class="nav-item  ">
          <Link className="nav-link" to ="/ValueFilter" >가치별★</Link>
        </li>
        <li class="nav-item">
          <Link  className="nav-link"  to="/companyfilter" >기업별★</Link>
        </li>
        <li class="nav-item">
          <Link  className="nav-link" to="/PostDetail" >착한소식</Link>
        </li>
       
       
      </ul>
      
    </div>
  </div>
</nav>

</section>
  </>
  );
};
export default withRouter(Pageslink_navbar);
